.bp-update-me {
    position: absolute;
    width: 100%!important;
    max-width: 100%!important;
    top: 0;
    left: 0;
    margin: 0;
    height: 100%;
    z-index: 9999;
}

.overflow-hidden {
    overflow: hidden!important;
}

.popup-overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    text-align: center;
    position: relative;
}

.popup-container {
    margin: 0;
    position: absolute;
    width: 75%;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    border: 1px solid #eeeeee;
    border-radius: 5px;
    box-shadow: 1px 1px 5px 5px #666666;
}

.popup-box {
    position: relative;
}

.popup-button {
    position: absolute;
    right: 0;
    color: #eeeeee;
    background-color: transparent;
    display: inline-block;
    text-decoration: none;
    font-size: 32px;
    line-height: 26px;
    margin: 0;
    padding: 10px 20px;
    cursor: pointer;
    white-space: nowrap;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    &:hover {
        text-decoration: none;
    }
}